.page{
  margin-top: 66px;
}

.about-us-section{
  background: #f1f1f1;
  padding: 20px; 
  height: 500px;
}


.about-us-section .border-image{
  border: 4px solid #4adafe;
  border-radius: 8px;
  width: 400px !important;
  height: 400px;
  position: relative;
}

.about-us-section .border-image img{
  width: 400px !important;
  border-radius: 8px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.box{
  display: flex;
  justify-content: center;
  align-items: center;
}
